// lib/users.ts
const API_BASE = "http://localhost:3001/api";

export type User = {
  id: number;
  email: string;
  role: "admin" | "staff" | "customer";
};

export async function getUsers(): Promise<any[]> {
  try {
    const res = await fetch("http://localhost:3001/api/users", {
      credentials: "include",
    });

    const data = await res.json();
    console.log("ðŸ”¥ RAW /api/users RESPONSE:", data);

    if (!res.ok) return [];

    // âœ… Backend tráº£ array trá»±c tiáº¿p
    if (Array.isArray(data)) {
      return data;
    }

    // âœ… Backend tráº£ { users: [...] }
    if (Array.isArray(data.users)) {
      return data.users;
    }

    return [];
  } catch (err) {
    console.error("GET USERS ERROR:", err);
    return [];
  }
}


export async function deleteUser(id: number): Promise<void> {
  const res = await fetch(`${API_BASE}/users/${id}`, {
    method: "DELETE",
    credentials: "include",
  });

  if (!res.ok) {
    throw new Error("Failed to delete user");
  }
}

export async function createUser(data: {
  email: string;
  password: string;
  role: "admin" | "staff";
}): Promise<User> {
  const res = await fetch(`${API_BASE}/users`, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    credentials: "include",
    body: JSON.stringify({
      email: data.email,
      password: data.password,
      password_confirmation: data.password,
      role: data.role,
    }),
  });

  const result = await res.json();

  if (!res.ok) {
    throw new Error(result.errors?.join(", ") || "Create user failed");
  }

  return result.user; // ðŸ‘ˆ backend báº¡n Ä‘ang render user
}
